.wtc_timeRange-wrap {
  /*margin: 20px;*/
  position: relative;
  display: inline-block;
  border-bottom: 1px solid #999;
  width: 438px;
}
.wtc_timeRange-wrap-business {
  width: 340px;
}
.wtc_timeRange-wrap-illegal {
  border-bottom: 1px solid red;
}
.wtc_timeRange-wrap-readonly {
  border-bottom: 1px solid #e5e5e5;
}
.wtc_timeRange-wrap .wtc_timeRange-separator {
  display: inline-block;
  color: #999;
}
.wtc_timeRange-wrap .wtc_timeRange-time-start {
  display: inline-block;
  position: relative;
  border-bottom: 1px solid #fff;
}
.wtc_timeRange-wrap .wtc_timeRange-time-end {
  display: inline-block;
  position: relative;
  border-bottom: 1px solid #fff;
}
.wtc_timeRange-wrap:not(.wtc_timeRange-wrap-readonly) .wtc_timeRange-time-active {
  border-bottom: 1px solid var(--theme-color);
}
.wtc_timeRange-wrap .wtc_timeRange-time-start-wrap,
.wtc_timeRange-wrap .wtc_timeRange-time-end-wrap {
  position: absolute;
  top: 0;
  right: 22px;
  height: 26px;
  font-size: 12px;
  color: #606266;
  max-width: 36px;
  text-align: center;
  display: flex;
  align-items: center;
}
.wtc_timeRange-wrap .wtc_timeRange-illegal-msg {
  position: absolute;
  bottom: -18px;
  left: 0;
  color: red;
  font-size: 12px;
}
.wtc_timeRange-wrap .kzt-date-picker__input .el-input__icon {
  line-height: 26px;
}
.kzt-picker-panel .wtc-datepicker-shortcut-disable {
  opacity: 0.4;
  cursor: not-allowed;
  pointer-events: none;
}
.kzt-picker-panel .wtc-datepicker-shortcut-disable:hover {
  color: #606266;
}
.kzt-picker-panel .el-picker-panel__footer {
  display: flex;
  justify-content: space-between;
  padding: 0 4px;
  height: 40px;
}
.kzt-picker-panel .el-radio--mini.is-bordered {
  padding: 12px 0px 0 0px;
}
.kzt-picker-panel .el-radio.is-bordered {
  border: none;
}
.kzt-picker-panel .el-radio.is-bordered + .kzt-picker-panel .el-radio.is-bordered {
  margin-left: 6px;
}
.kzt-picker-panel .kzt-picker-panel__footer-confirm {
  margin-top: 5px;
  margin-right: 8px;
}
.kzt-picker-panel .kzt-date-picker__header-right {
  font-size: 14px;
  color: #212121;
  font-family: Semibold;
}
/*.kzt-picker-panel .el-date-table td, .kzt-picker-panel .el-date-table td div {*/
/*height: 26px;*/
/*}*/
.kzt-picker-panel .kzt-date-picker__header-left {
  width: 278px;
  text-align: center;
}
.kzt-picker-panel .kzt-date-picker__header-right {
  width: 118px;
  text-align: center;
}
.kzt-date-picker__header-left .el-icon-d-arrow-left {
  margin-right: 6px;
}
.kzt-date-picker__header-left .el-icon-d-arrow-right {
  margin-left: 6px;
}
.kzt-picker-panel .kzt-picker-panel__content-date {
  padding: 0;
}
.kzt-picker-panel .el-date-table td span {
  border-radius: 2px;
}
.kzt-picker-panel .kzt-picker-panel__footer-confirm .el-button {
  background-color: var(--theme-color);
  border: none;
  color: #fff;
}
.kzt-picker-panel .el-button.is-plain:focus,
.kzt-picker-panel .el-button.is-plain:hover {
  background: var(--theme-color);
  border-color: none;
  color: #fff;
  opacity: 0.8;
}
.kzt-picker-panel .el-date-table td.end-date span,
.kzt-picker-panel .el-date-table td.start-date span {
  background-color: var(--theme-color);
}
.kzt-picker-panel .el-date-table td.available:hover {
  color: var(--theme-color);
}
.kzt-picker-panel .el-date-picker__header-label.active,
.kzt-picker-panel .el-date-picker__header-label:hover {
  color: var(--theme-color);
}
.kzt-picker-panel .el-picker-panel__icon-btn:hover {
  color: var(--theme-color);
}
/*.kzt-picker-panel .el-radio .el-radio__label {
    color: var(--theme-color);
}
.kzt-picker-panel .el-radio__input.is-checked + .el-radio__label {
    color: #3761CA;
}*/
.kzt-picker-panel .el-radio .el-radio__label:hover {
  color: var(--theme-color);
}
.kzt-picker-panel .el-radio__input.is-checked + .el-radio__label {
  color: var(--theme-color);
}
.kzt-picker-panel .el-date-table td.current:not(.disabled) span {
  background-color: var(--theme-color);
  color: #fff;
}
.kzt-picker-panel .el-year-table td .cell:hover,
.kzt-picker-panel .el-year-table td.current:not(.disabled) .cell {
  color: var(--theme-color);
}
.kzt-picker-panel .el-month-table td .cell:hover {
  color: var(--theme-color);
}
.kzt-picker-panel .el-date-table td.today span {
  color: var(--theme-color);
}
.kzt-picker-panel .el-month-table td.today .cell {
  color: var(--theme-color);
}
/* 修改elementui字体图标的指向，需指向kdfont */
.wtc_timeRange-wrap .el-icon-time,
.wtc_timeRange-wrap .el-icon-circle-close,
.kzt-picker-panel .el-icon-arrow-left,
.kzt-picker-panel .el-icon-arrow-right,
.kzt-picker-panel .el-icon-d-arrow-left,
.kzt-picker-panel .el-icon-d-arrow-right {
  font-family: kdfont !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.wtc_timeRange-wrap .el-icon-circle-close {
  font-size: 14px;
}
.wtc_timeRange-wrap .el-icon-time:before {
  content: "\e648";
  /*日历*/
}
.wtc_timeRange-wrap .el-icon-circle-close:before {
  content: "\ea42";
  /*关闭*/
}
.kzt-picker-panel .el-icon-arrow-left:before {
  content: "\ecdb";
  /*向左单箭头*/
}
.kzt-picker-panel .el-icon-arrow-right:before {
  content: "\ecdc";
  /*向右单箭头*/
}
.kzt-picker-panel .el-icon-d-arrow-left:before {
  content: "\ecd7";
  /*向左双箭头*/
}
.kzt-picker-panel .el-icon-d-arrow-right:before {
  content: "\ecd6";
  /*向右双箭头*/
}
.wtc_timeRange-wrap .el-input.is-disabled .el-input__inner {
  background-color: transparent;
}
.el-time-spinner__wrapper.hideMinuteSpinner {
  width: 100%;
}
.kzt-picker-panel.hideTimePicker {
  width: 291px !important;
}
.kzt-picker-panel .kzt-picker-panel__content.hideTimePicker {
  width: 291px !important;
}
.kzt-date-picker__header-right.hideTimePicker {
  display: none;
}
